* {
    margin: 0;
}

header {
    height: 80px;
    background-color: rgb(119, 24, 24);
    position: relative;
    padding: 0 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo a {
    color: rgb(255, 255, 255);
    font-size: 0.8em;
    margin: 0;
    text-decoration: none;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

nav {
    background-color: rgb(119, 24, 24);
    box-shadow: 0 0px 10px rgba(0, 0, 0, 0.71);
    font-size: 1.4em;
    position: absolute;
    right: 0;
    top: 0;
    height: 100vh;
    width: 250px;
    display: none;
}

nav ul {
    width: 100%;
    list-style-type: none;
    text-align: center;
    margin: 0  auto;
    padding: 0;
}

nav li {
    padding: 0.8em 0;
    width: 100%;
}

nav a {
    color: white;
    text-decoration: none;
    padding: 0 1em;
}

nav li:hover {
    background-color: rgb(94, 15, 15);
    cursor: pointer;
}

.menu-btn {
    display: flex; 
    align-items: center;
    padding: 8px 15px;
    background-color: rgb(119, 24, 24);
    border: white solid 2px;
    cursor: pointer;
}

.open {
    margin: 0 1em;
}

.menu-btn:hover {
    background-color: rgb(94, 15, 15);
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.421);
}

.menu-icon {
    margin-right: 10px;
}

.bars {
    display: block;
    width: 20px;
    height: 3px;
    background-color: white;
    margin: 5px 0;
}

.menu-text {
    font-size: 1.2em;
    color: white;
}

.close {
    margin: 1em 1.5em;
    padding: 10px 15px;
}

.cross1 {
    width: 25px;
    transform: rotate(45deg);
    position: relative;
    top: 5px;
}

.cross2 {
    width: 25px;
    transform: rotate(135deg);
    position: relative;
    top: -3px;
}

main {
    max-width: 800px;
    width: 80%;
    margin: 0 auto;
    font-size: 1.2em;
}

h2 {
    margin: 1em 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

main p {
    font-size: 1.2em;
    line-height: 1.4em;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

@media only screen and (min-width: 992px) {

header {
    padding: 0 3em;
    position: static;
}

.menu-btn {
    display: none;
}

#open-menu {
    display: none !important;
}

nav {
    display: block !important;
    position:static;
    box-shadow: 0 0px 0px;
    right: 0;
    top: 0;
    height: auto;
    font-size: 1.4em;
    width: auto;
}

nav ul {
    height: 100%;
}

nav li {
    display: inline;
    padding: 1.2em 0;
}
}